import Foot from '~/components/Foot'
import Head from '~/components/Head'
export default class AdminNotice extends Component{
  render(){
    return(
      <div style={{height:'100vh', backgroundColor:'#fff'}}>
        <Head title='公告管理'></Head>
        <div style={{padding:'10px',boxSizing:'border-box'}}>
          <p style={{marginTop:'10px'}}>1. 严禁在图书馆内吸烟。</p>
          <p style={{marginTop:'10px'}}>2. 保持室内整洁，禁止将食物及饮料带入馆内；请勿随意搬拽阅览桌椅。</p>
          <p style={{marginTop:'10px'}}>3. 保持室内安静：入馆请将呼机和手机关机或调为静音状态；在馆内请轻声交谈，以免影响他人阅读。</p>
          <p style={{marginTop:'10px'}}>4. 尊重管理人员，做个文明读者。</p>
          <p style={{marginTop:'10px'}}>5. 爱护书刊资料及一切公共财物，请勿涂抹、撕毁、私藏书刊。</p>
        </div>
        <Foot></Foot>
      </div>
    )
  }
}